.contacto {
    display: grid;
    margin-top: 10px;
    padding: 20px;
    justify-content: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 200px 1fr 150px;
    background: #393737;
    grid-template-areas:
    ". h1con ."
    ". form ."
    ". button ."
} 


.contacto h2 {
    display: flex;
    grid-area: h1con;
    margin: 0;
    height: 20px;
    justify-self: center;
    align-items: center;
    color: white;
}

.contacto label {
    text-align: left;
    height: 20px;
}

.ui.form .field>label {
    color: white;
}

.contacto input {
    height: 30px;
}

.contacto .input {
    height: 30px;
}

.contacto .field {
    height: 80px;
}

.contacto button {
    grid-area: button;
    display: grid;
    justify-self: center;
    width: 200px;
    height: 40px;
}

.spinner {
  width: 40px;
  height: 40px;
  margin: 100px auto;
  background-color: rgb(255, 255, 255);

  border-radius: 100%;  
  -webkit-animation: sk-scaleout 1.0s infinite ease-in-out;
  animation: sk-scaleout 1.0s infinite ease-in-out;
}

@-webkit-keyframes sk-scaleout {
  0% { -webkit-transform: scale(0) }
  100% {
    -webkit-transform: scale(1.0);
    opacity: 0;
  }
}

@keyframes sk-scaleout {
  0% { 
    -webkit-transform: scale(0);
    transform: scale(0);
  } 100% {
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
    opacity: 0;
  }
}

@media (max-width: 767px) {
    .contacto {
        display: grid;
        margin-top: 10px;
        padding: 80px 20px 20px 20px;
        justify-content: center;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 80px 1fr 160px;
        background: #393737;
        grid-template-areas:
        ". h1con ."
        ". form ."
        ". button .";
    } 


    .contacto h2 {
        display: flex;
        grid-area: h1con;
        margin: 0;
        justify-self: center;
        align-items: center;
        color: white;
    }

    .contacto label {
        text-align: left;
        height: 20px;
    }

    .ui.form .field>label {
        color: white;
    }

    .contacto input {
        height: 30px;
    }

    .contacto .input {
        height: 30px;
    }

    .contacto .field {
        height: 60px;
    }

    .contacto button {
        grid-area: button;
        display: grid;
        justify-self: center;
        width: 200px;
        height: 40px;
    }
}

@media (max-width: 399px) {
    .contacto {
        grid-template-rows: 80px 1fr 240px;
    }
}

@media (max-width: 360px) {
    .contacto {
        grid-template-rows: 80px 1fr 30px;
    }
}

@media (max-width: 767px) and (orientation: landscape) { 
    .contacto {
        height: 200%;
        grid-template-rows: 80px 1fr 30px;
    }
}

@media (width: 812px) and (orientation: landscape) { 
    .contacto {
        height: 200%;
        grid-template-rows: 80px 1fr 120px;
    }
}

@media (width: 320px) {
    .contacto {
        height: 110%;
        grid-template-rows: 80px 1fr 25px;
    }
}